import router from '../router'

const HeaderComponent = {
    template: `<header class="head_wrap">
    <div class="head_bi">
        <h1> </h1>
        </div>
            <div class="head_info">
                <span class="btn_out" v-on:click="DestorySessionData"></span>
            </div>
</header>`,
    methods:{
        DestorySessionData(){
            var y = confirm(" ?");
            if (y == true) {
                sessionStorage.clear();
                router.push({
                    path: '/',
                    name: 'login'
                  })
            }
        }
    }

}

export default HeaderComponent;