import eventBus from './eventbus.js';

const joinModal = {
    template: `<div class="pop-window fade" id="modal-join">
                <div class="alert">
                    <div class="alert_con">
                        <i class="material-icons">error_outline</i>
                        <h3></h3>
                        <ul class='inputs'>
                            <form>
                            <li>
                                <input type='text' id='user_id' placeholder=' ID'>
                            </li>
                            <li>
                                <input type='password' id='user_pw_re' placeholder=''>
                            </li>
                            <li>
                                <input type='password' id='user_pw' placeholder=' '>
                            </li>
                            <li>
                                <input type='text' id='user_class' placeholder='' readonly value='' style='color:#444'>
                            </li>
                            <li>
                                <input type='text' id='user_name' placeholder=''>
                            </li>
                            <li>
                                <input type='text' id='user_phone' placeholder=''>
                            </li>
                            </form>
                        </ul>
                    </div>

                    <div class="modal_foot">
                        <span  class="b_red" v-on:click='insertData'></span>
                        <span v-on:click='ModalClose' class="b_sgrey"></span>
                    </div>
                </div>
            </div>`,
    methods: {
        ModalClose() {
            const Modal = document.getElementById('modal-join')
            Modal.style.opacity = '0';
            setTimeout(() => {
                Modal.style.display = 'none';
            }, 100);
        },
        insertData(){
            const userId = document.getElementById('user_id')

            const userpwRe = document.getElementById('user_pw_re')
            const userpw = document.getElementById('user_pw')

            const userClass = document.getElementById('user_class')
            const userPhone = document.getElementById('user_phone')
            const userName =  document.getElementById('user_name')
            if(userId.value.length < 8){
                alert('id 8  ')
                userId.focus()
            }
            else if(userpw.value < 10){
                alert(' 10  ')
                userpw.focus()

            }
            else if(userpwRe.value != userpw.value){
                alert(' ')
                userpwRe.focus()
            }
            else if(userClass.value ==""){
                alert('')
                userClass.focus()
            }
            else if(userName.value < 2){
                alert(' ')
            }
            else if(userPhone.value < 2){
                alert(' ')
            }
            else{
                const baseURI = 'api/join.user.php';
                axios.post(`${baseURI}`, {
                        userId:userId.value,
                        userPw:userpw.value,
                        userClass:userClass.value,
                        userName:userName.value,
                        userPhone:userPhone.value
                    })
                    .then((result) => {
                        if(result.data.result == 'idno'){
                            alert('  ')
                            userId.focus()
                        }
                        else{
                            if(result.data.phpResult == 'ok'){
                                alert('    .')
                                // this.ModalClose();
                                location.reload();
                            }
                            else{
                                alert(' ')
                            }
                        }

                           
                    })
                    .catch(err => console.log('Login: ', err));
            }
           
        }

    }

}

export default joinModal;